body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
    /* background-image: url(https://filebox.tymoon.eu//file/TVRVeU5nPT0=); */
    background-image: url(https://images.wallpaperscraft.com/image/landscape_art_road_127350_1280x720.jpg);
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
}
.homeTransition-appear {
    opacity: 0;
}

.homeTransition-appear.homeTransition-appear-active {
    opacity: 1;
    transition: all 0.5s ease-in-out;
}

#aboutMe {
    text-align: center;
    height: 90vh;
    background-color: rgba(0, 21, 59, 0.7);
    display: flex;
    justify-content: center;
    align-items: center;
}

#pp {
    font-family: 'Montserrat';
    font-size: 5vh;
    color: #FFF;
}


.portfolioTransition-appear {
    opacity: 0;
}

.portfolioTransition-appear.portfolioTransition-appear-active {
    opacity: 1;
    transition: all 0.5s ease-in-out;
}

.title {
    text-align: center;
    border-radius: 5px;
    font-family: 'Montserrat';
}

.square {
    background-color: rgba(255, 255, 255, 0.4);
    margin-bottom: 10vh;
    width: 300px;
    height: 300px;
    border-radius: 5px;
    overflow: hidden;
    box-shadow: 0 0 1rem 0 rgba(0, 0, 0, 0.3);

}

.square:hover {
    box-shadow: 0 0 1rem 0 rgba(255, 255, 255, 0.2)
}

.container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
    margin-top: 10vh;
}


p {
    text-align: center;
    font-weight: 200;
}


a.portButt {
    text-decoration: none;
    list-style: none;
    color: black;
}



.twoButt {
    display: flex;
    justify-content: space-evenly;
}

#mundo {
    text-align: center;
}

@media all and (max-width: 1920px) {
    .square {
        margin-left: 20vh;
        margin-right: 20vh;
    }
}

@media all and (max-width: 1000px) {
    .square {
        margin-left: 0;
        margin-right: 0;
    }
}
.contactTransition-appear {
    opacity: 0;
}

.contactTransition-appear.contactTransition-appear-active {
    opacity: 1;
    transition: all 0.5s ease-in-out;
}

